* {
  margin:0;
  padding:0;
}

.container{
  background: linear-gradient(19deg, #3EECAC, #EE74E1);
  width:100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-wrapper{
  background-color: rgb(224, 220, 220);
  box-shadow: -15px -15px 15px rgba(255,255,255,0.2),
  15px 15px 15px rgba(0,0,0,0.1),
  inset -5px -5px 5px rgba(255,255,255,0.2),
  inset 5px 5px 5px rgba(0,0,0,0.1);
  min-width: 350px;
  min-height: 500px;
  padding: 5px;
  box-sizing: border-box;
  border-radius: 5px;
}

.title{
  color: #EE74E1;
  text-align: center;
  margin: 30px 0px 40px 0px;
}

.name,
.email,
.password{
  margin: 8px;
  color: #969292;
  font-size: 16px;
  padding: 3px;
}

.password{
  margin-bottom: 40px;
}

.input{
  height: 32px;
  font-size: 20px;
  outline: none;
  width: 100%;
  border-top-style: none;
  border-left-style: none;
  border-right-style:none;
  border-bottom-style: 2px solid black;
  margin-bottom: 10px;
}

.error{
  color:red;
}

.submit{
  display: flex;
  text-transform: uppercase;
  font-size: 20px;
  border-radius: 25px;
  color: white;
  padding: 10px 60px;
  background: linear-gradient(19deg, #3EECAC, #EE74E1);
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
  rgba(0, 0, 0, 0.22) 0px 15px 12px;
  margin: auto;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.1s;
}
button:active{
  transform: translateY(2px);
}

/* Dashbord style*/
.app-container{
  display: flex;
  flex-direction: column;
  background: linear-gradient(19deg, #3EECAC, #EE74E1);
  width:auto;
  height: auto;
  gap:10px;
  padding: 1rem;
}
table{
  border-collapse: collapse;
  width:100%;
}
th,
td{
  border:1px solid #ffffff;
  text-align: left;
  padding: 8px;
  font-size: 32px;
}

th{
  background-color: #EE74E1;
}

td{
  background-color: rgb(117, 201, 250);
}
.form {
  display: flex;
  gap: 5px;
}
.form td:last-child{
  display: flex;
  justify-content: space-evenly;
}

.form * {
  font-size: 22px;
}

h2{
  font-size: 30px;
}
